import React, { Component } from 'react';
import '../styles/styles.css';
import FadeInSection from './fadeup';
import Typewriter from 'typewriter-effect';
import logo from '../assets/imgs/oh.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF } from "@fortawesome/free-brands-svg-icons"

class HomePage extends Component {
    render() {
        return (
            <div>
                <main>
                    <div id="index">
                        <FadeInSection id="index">
				{/* eslint-disable-next-line */}
                                <img src={logo} alt="Error loading the image" />
                                <h1 id="name">Omar Hosam</h1>
                            <span>I'm a</span>
                                <div id="typewriter">
                                <Typewriter
                                        options={{
                                            strings: ['Programmer',
                                            'Editor',
                                            'Digital Artist'],
                                            autoStart: true,
                                            loop: true,
                                        }}
                                    />
                                </div>
                        </FadeInSection>
                    </div>
                    <FadeInSection>
                        <h1 style={{fontSize: 50}}>Who Am I</h1>
                        <p>My name is Omar Hosam and I'm from Egypt<br/> I'm 13 Years Old Right Now, I was born in Saudi Arabia but my parents moved to Egypt where my life begins.<br/>I started learning programming at the age of 9 and started Web Developing at the age of 13

                        <br/>I like making programs with Python, Javascript, HTML, CSS and Hacking games with C++ and cheat engine

                        <br/>Besides programming I also like Video Editing and Digital Art with Adobe Illustrator and Gaming</p>
                    </FadeInSection>
                    <FadeInSection>
                        <h1>Projects</h1>

                        <p className="bold">
                        Watermelon
                        </p>
                        <p>
                        This website was made for <code>Certain time video sharing</code> But Unfortunatly Youtube added the new <code>Copy video URL at current time</code> But the page is still Upon running on github pages, the URL is <a href="https://omarhosam.github.io/watermelon/">Here</a>
                        </p>
                        <p className="bold">
                        Black2White
                        </p>
                        <p>
                        This project was inspired by <a href="https://www.youtube.com/c/NeuralNine">NeuralNine</a>, This project was made to transform Black and White images into colored image using some models and artifitial intelegence
                        </p>
                        <p className="bold">
                        double click auto clicker
                        </p>
                        <p>
                        This program was the first of its kind because it was the first double click auto clicker that was written with Python Programming language, All it does is just listening for your mouse events When you clicks it clicks back so you double clicked
                        </p>

                    </FadeInSection>
                    <FadeInSection>
                        <h1>Contact</h1>
                        <h3><FontAwesomeIcon icon={faFacebookF} /> Facebook</h3>
                        <p>
                        The easiest way to find me is on <a href="https://facebook.com/pwnkid">facebook</a>
                        </p>
                        <h3>There is no other way to contact</h3>
                    </FadeInSection>
                </main>
            </div>
        );
    }
}
 
export default HomePage;